<template>
  <!-- 
    1.   ,      .
    2.    ,   > 
    (   this. = )
     data  
    -    +     console   !!!
    3.   (div  p    .)
    v-for=" in " :key=
           .
    listItem[0].TITLE / listItem[1].MAIN_IMG_THUMB
      
    .TITLE / .MAIN_IMG_THUMB    .
    
    -->
    <!-- #4b473c -->
    <div class="w-full bg-[#f7f8f9] px-[2%] pb-12 pt-32 box-border">
      <div class="max-w-7xl mx-auto box-border my-3">
        <router-link :to="'/'" class="cursor-pointer">
          <img src="@/assets/main_logo2.png" alt="logo">
        </router-link>
      </div>
      <div class="max-w-7xl mx-auto flex flex-wrap gap-x-[1%]">
        <ul class="flex justify-center bg-white rounded-md border box-border p-4 basis-full mb-4 flex-wrap gap-x-4">
          <li @click="cateName=''" class="cursor-pointer text-xs md:text-base basis-[20%] sm:basis-[15%] md:basis-[10%] lg:basis-[8%] text-center mb-2 sm:mb-0" :class="cateName === '' && 'font-extrabold text-orange-500'"></li>
          <li @click="cateName= e.tourSe " v-for="e in cateList" :key="e" class="cursor-pointer text-xs md:text-base basis-[20%] sm:basis-[15%] md:basis-[10%] lg:basis-[8%] text-center mb-2 sm:mb-0" :class="cateName === e.tourSe && 'font-extrabold text-orange-500'"> {{ e.tourSe }} </li>
          <!--   2  {{ e.tourSe }}     class =  click =   {{  }}    e.tourSe . -->
        </ul>

        <div class="relative mt-4">
          <swiper
            :modules="modules"
            :slides-per-view="2"
            :breakpoints="{ 640:{ slidesPerView:3 } }"
            :space-between="50"
            :scrollbar="{ draggable: true }"
            :mousewheel="true"
            @swiper="onSwiper"
            @slideChange="onSlideChange" 
          >
          <SwiperSlide v-for="e in cateItem" :key="e" class="bg-white !w-[30%] !h-[500px] sm:!w-[48%] lg:!w-[30%] my-4 box-border border rounded-md p-5 cursor-pointer">
            <h3 class="mb-4 text-lg">{{ e.tourNm }}</h3>
            <img :src="e.thumbImg" :alt="e.tourNm" class="w-[400px] sm:w-full mb-6 mx-auto">
            <ul>
              <li> : {{ e.telno }}</li>
              <li> : {{ e.areaSe }}</li>
              <li><button v-if="e.tourUrl !== ''" @click="open_page(e.hmpg)"></button></li>
            </ul>
            <router-link :to="'/detail/'+(e.tourNo)" @click="ResultList(e.tourNo)"></router-link> 
          </SwiperSlide>
          </swiper>
        </div>
        <!-- <div v-for="(e) in cateItem" :key="e" class="bg-white basis-full sm:basis-[49%] lg:basis-[32.5%] mb-4 box-border border rounded-md p-5 cursor-pointer"> -->
          
          <!-- <p @click="ResultList(e.tourNo)"></p> -->
        <!-- </div> -->
        <div class="basis-full bg-white flex justify-around">
          <ul class="flex justify-center">
            <li v-for="e in totalCount / pageList" :key="e" class="cursor-pointer" :class="e === page && 'font-extrabold text-orange-500'" @click="page = e; getItem()">{{ e }}</li>
          </ul>
        </div>
      {{ page }}
    </div>
  </div>
</template>

<script>
// https://tour.chungbuk.go.kr/openapi/tourInfo/attr.do
// import axios from 'axios';
// @/assets/attractionAPI.json
import information from '@/assets/attractionAPI.json'
import { Swiper, SwiperSlide } from 'swiper/vue';
import 'swiper/css';
import { Mousewheel, Scrollbar } from 'swiper';
export default {
  name: 'App',
  components: {
    Swiper,
    SwiperSlide
  },
  setup() {
    const onSwiper = (swiper) => {
      console.log(swiper);
    };
    const onSlideChange = () => {
        console.log('slide change');
    };
    return {
      onSwiper,
      onSlideChange,
      modules: [Mousewheel, Scrollbar],
    }
  },
  data() {
    return {
      pageList: 30,
      listItem: information.result, // @/assets/attractionAPI.json import   information     listItem information .     listItem information       information API   result  .  listItem information  information.result  .
      page: 1,
      totalCount : 0,
      cateName : "",
    }
  },
  methods:{
    ResultList(e){ // ResultList(e)  ResultList(e.UC_SEQ)    e   e.UC_SEQ .  e    abcde    abcde e.UC_SEQ .  ResultList     session storage .
      return this.listItem.filter((data)=>{
        if(data.tourNo === e){
          return this.session(data) // session 
          // return console.log(data)
        }
      })
    },
    session(data){
      sessionStorage.setItem('data',JSON.stringify(data)); //   JSON  sessionStorage .
    },
    open_page(url){
      window.open(url);
    },
    //  window.opem(url)   methods open_page(url)  .
    // getItem: function(){
    //   axios.get(`@/src/assets/attractionAPI.json`)
    // .then((res)=>{
    //   this.listItem = res.data
    //   console.log(res.data)
    // })
    // .catch((error)=>{
    //   console.log(error)
    // })
    // }
  },
  mounted(){
    console.log("Create  ")
  },
  created(){
    console.log(" ")
    console.log(this.listItem)

    // axios.get(`@/src/assets/attractionAPI.txt`)
    // .then((res)=>{
    //   console.log(res.data);
    //   this.totalCount = res;
    //   this.listItem = res
    // })
    // .catch((error)=>{
    //   console.log(error)
    // })
    

  },
  computed: {
    cateList(){
      return this.listItem.filter((item,i)=>{
        return(
          this.listItem.findIndex((item2)=>{
            return item.tourSe === item2.tourSe
          }) === i
        )
      })
    },
    //  ,          .
    cateItem(){
      return this.listItem.filter((data)=>{

        if(this.cateName !== ''){
          return data.tourSe === this.cateName
        }else{
          return data.tourSe
        }
        //             .
      })
    }
  },
  // computed -       -     .
  // watch -    
  //     coumputed 
  // created -     
  // mounted - ,      created   >  /   
  //   create  DOM (mounted) (update)  (destory) 4  .
}
</script>

